.settings-page-container {
  display: flex;
  min-height: 100vh;
  background-color: #f9f9f9;
}

/* Sidebar Styling */
.sidebar {
  flex: 0 0 250px; /* Fixed width for sidebar */
  background-color: #2c3e50;
  color: #ecf0f1;
  padding: 20px;
  min-height: 100vh; /* Ensure the sidebar takes full height */
}

/* Content Area Styling */
.settings-content {
  flex: 1;
  padding: 20px;
  background-color: #ffffff;
  border-left: 1px solid #ddd;
  margin-left: 250px; /* Ensure content doesn't overlap sidebar */
}

/* Headings */
.settings-content h2 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #34495e;
}

.settings-content h3 {
  font-size: 20px;
  margin-top: 30px;
  color: #34495e;
}

/* Paragraph Styling */
.settings-content p {
  margin-bottom: 15px;
  color: #7f8c8d;
}

/* Form Styles */
form {
  max-width: 1100px;
  margin-top: 10px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
  color: #2c3e50;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
  background-color: #fefefe;
}

/* Available Days Checkbox Group */
.checkbox-group {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.checkbox-group label {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 14px;
  color: #34495e;
}

.checkbox-group input {
  margin-right: 5px;
}

/* Button Styling */
button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.save-button {
  background-color: #207adb;
  color: #ffffff;
}

.save-button:hover {
  background-color: #3378e7;
}

.close-modal-button {
  background-color: #3498db;
  color: #ffffff;
}

.close-modal-button:hover {
  background-color: #2980b9;
}

/* Error and Success Messages */
.error-message {
  color: #e74c3c;
  font-weight: bold;
  margin-bottom: 15px;
}

.success-message {
  color: #27ae60;
  font-weight: bold;
  margin-bottom: 15px;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal {
  background: #ffffff;
  padding: 20px;
  border-radius: 10px;
  width: 400px;
  text-align: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.modal h3 {
  margin-bottom: 15px;
  font-size: 20px;
  color: #34495e;
}

.modal p {
  margin-bottom: 20px;
  color: #7f8c8d;
}
